/* google font */
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;800&family=VT323&display=swap");

/* layout */
:root {
  --primary: #1aac83;
  --error: #e7195a;
}
body {
  background: #f1f1f1;
  margin: 0;
  font-family: "Poppins";
}
header {
  background: #fff;
}
header .navbarContainer {
  max-width: 1400px;
  margin: 0 auto;
  /* 10 is up and down, 20 is left and right */
  padding: 10px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

header a {
  color: #333;
  text-decoration: none;
}
.pages {
  max-width: 1400px;
  padding: 20px;
  margin: 0 auto;
}

.navbarContainer label {
  display: none;
}

a {
  text-decoration: none;
  color: black;
}

.emailAndLogOutContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 15rem;
}
.loginAndSignupContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 8rem;
}
.logout {
  background: #fff;
  color: var(--primary);
  border: 2px solid var(--primary);
  padding: 6px 10px;
  border-radius: 10px;
  font-family: "Poppins";
  cursor: pointer;
  font-size: 1em;
}
.error {
  color: var(--error);
}
fieldset {
  border: none
}
form.login {
  max-width: 400px;
  margin: 40px auto;
  padding: 20px;
  background: #fff;
  border: 4px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 20rem;
}
form.login input,
form.signup input[type="text"],
form.signup input[type="password"],
form.signup input[type="email"] {
  width: 100%;
}

form.signup {
  max-width: 400px;
  margin: 2px auto;
  padding: 20px;
  background: #fff;
  border: 4px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 40rem;
}

.loginFormButton, .signupSubmitButton {
  color: #fff;
  background-color: var(--primary);
  border: none;
  height: 2rem;
  width: 5rem;
  border-radius: 10rem;
  cursor: pointer;
}

.note {
  border: 1px solid black;
  width: 15rem;
}
